.portfolio_button {
  align-items: center;
  appearance: none;
  background-color: #fff;
  border-radius: 24px;
  border-style: none;
  box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px, rgba(0, 0, 0, .14) 0 6px 10px 0, rgba(0, 0, 0, .12) 0 1px 18px 0;
  cursor: pointer;
  font-size: 20px;
  padding: 5px 20px;
  position: relative;
  text-align: center;
  transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1), opacity 15ms linear 30ms, transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
  width: auto;

  &:hover {
    background: #F6F9FE;
    color: #e73919;
  }

  &:not(:disabled):hover {
    box-shadow: rgba(60, 64, 67, .3) 0 2px 3px 0, rgba(60, 64, 67, .15) 0 6px 10px 4px;
  }
}

.plus_icon_container {
  cursor: pointer;
  border-radius: 50%;
  padding: 5px 5px 0;
}

.plus_icon {
  border: none;
}

.plus_icon img {
  width: 1.25rem;
  height: 1.25rem;
}

.plusIcon svg {
  width: 20px;
  height: 20px;
 }

@media (max-width: 768px) {
  .portfolio_button {
    font-size: 12px;
    padding: 5px 20px;
  }
  .plus_icon_container {
    padding: 3px 3px 0;
  }

  .plus_icon {
    padding: 8px;
  }

  .plus_icon img {
    width: 1rem;
    height: 1rem;
  }
  .navbar {
    align-items: flex-start;
  }
}
